export default {
	components: {

	},
	data() {
		return {
			showPinMask: false,
			id: '',
			level: '',
			ratio: '',
			name: '',
			password:'',
			balance:''
		};
	},

	onLoad(options) {
		this.id = options.id
		//产品详情
		uni.request({
			url: this.baseUrl + "/product-detail",
			data: {
				Id: this.id
			},
			header: {
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (this.$base._indexOf(res.data.status)) {
					this.$base._isLogin()
				} else if (res.data.status == 1) {
					this.level = res.data.data.NeedLevel
					this.name = res.data.data.Name
					this.ratio = res.data.data.Ratio

				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}
			}
		})
		//账户余额
		uni.request({
			url: this.baseUrl + "/balance-list",
			data: {
				page: 1,
				count:1
			},
			header: {
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (this.$base._indexOf(res.data.status)) {
					this.$base._isLogin()
				} else if (res.data.status == 1) {
					this.balance = res.data.data.Balance
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}
			}
		})
	},
	onPullDownRefresh() {

	},
	methods: {
		closePinMask() {
			this.showPinMask = false
		},
		comfirmBook() {
			this.showPinMask = true
		},
		confirm() {
			//提交密码预约
			uni.request({
				url: this.baseUrl + "/plan-product",
				data: {
					Id: this.id,
					PayPassword: this.password
				},
				method: 'POST',
				header: {
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					if (this.$base._indexOf(res.data.status)) {
						this.$base._isLogin()
					} else if (res.data.status == 1) {
						this.showPinMask = false
						uni.showModal({
							title: '预约中..',
							content: '请耐心等待预约结束',
							success: function(res) {
								if (res.confirm) {
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});


					} else {
						this.showPinMask = false
						uni.showModal({
							title: '预约失败',
							content: '今日预约名额已满，无法进行预约',
							success: function(res) {
								if (res.confirm) {
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
					}
				}
			})
		},
		jumpToForgetPassword(){
			this.showPinMask = false
			uni.navigateTo({
				url:"../login/forgetPassword"
			})
		}

	}
}
